.description {
  font-size: 50px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1;
      flex: 1;
}
.postListWrapper {
  width: 52%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 70px;
}
.postListWrapper .postPreview {
  border-bottom: 1px solid #eee;
  cursor: pointer;
}
.postListWrapper .postPreview:last-of-type {
  border-bottom: 0;
}
.postListWrapper .postPreview p {
  margin: 0;
}
.postListWrapper .postPreview a {
  color: #a3a3a3;
}
.postListWrapper .postTitle {
  margin-top: 30px;
  color: #404040;
  font-size: 26px;
}
.postListWrapper .postTitle:hover {
  color: #0085a1;
}
.postListWrapper .postTitle:hover a {
  color: #0085a1;
}
.postListWrapper .postContent {
  font-size: 14px;
  font-style: italic;
  color: #a3a3a3;
}
.postListWrapper .postContent:hover {
  color: #0085a1;
}
.postListWrapper .postContent:hover a {
  color: #0085a1;
}
.postListWrapper .postContent img {
  width: 100%;
}
.postListWrapper .postTime {
  padding-bottom: 30px;
  line-height: 1.7;
  font-family: Lora, 'Times New Roman', serif;
  color: #ccc;
  font-size: 18px;
  font-style: italic;
}
@media (max-width: 1440px) {
  .postListWrapper {
    width: 750px;
  }
}
@media (max-width: 768px) {
  .postListWrapper {
    margin-left: 15px;
    margin-right: 15px;
    width: calc(100% - 30px);
  }
}
@media (min-width: 1440px) {
  .postListWrapper {
    width: 52%;
  }
}
@media (min-width: 2650px) {
  .postListWrapper {
    width: 1380px;
  }
}
