.nav {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 15px;
}
.nav a {
  padding: 20px;
  text-transform: uppercase;
  color: #404040;
}
.nav .logo {
  font-size: 18px;
  font-weight: 800;
}
.nav .link {
  font-size: 12px;
  font-weight: 800;
}
.nav.hasBannerImg a {
  color: #fff;
}
