.banner {
  position: relative;
  margin-bottom: 20px;
  background: no-repeat center;
  padding: 150px 0;
  color: #fff;
}
.banner .mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
}
.banner span {
  font-size: 18px;
}
.banner .indexTitleWrap,
.banner .postTitleWrap {
  position: relative;
  z-index: 2;
}
.banner .indexTitleWrap {
  text-align: center;
}
.banner .postTitleWrap {
  margin: auto;
  text-align: left;
}
.banner .postTitleWrap .postTitle {
  font-size: 55px;
}
.postTitle {
  margin: 0;
  margin-bottom: 20px;
  font-size: 80px;
  font-weight: 700;
}
.postTime {
  font-family: Lora, 'Times New Roman', serif;
  color: #ccc;
  font-size: 18px;
  font-style: italic;
  padding-bottom: 30px;
}
.bannerWithoutImg {
  margin-left: auto;
  margin-right: auto;
  padding: 85px 0 20px;
  width: 52.08%;
  color: #404040;
}
.bannerWithoutImg h1 {
  margin: 20px 0;
  line-height: 60px;
  font-size: 50px;
}
@media (min-width: 768px) {
  .postTitleWrap,
  .bannerWithoutImg {
    width: 750px;
  }
  .postTitleWrap .postTitle {
    font-size: 30px;
  }
}
@media (min-width: 1440px) {
  .postTitleWrap,
  .bannerWithoutImg {
    width: 52.08%;
  }
}
@media (min-width: 2650px) {
  .postTitleWrap,
  .bannerWithoutImg {
    width: 1380px;
  }
}
